.header {
  background: #000;
  position: relative;
  padding: 5px;
}

.header::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px; /* Adjust this value to control the border height */
  background: linear-gradient(
    to right,
    #000,
    white,
    #000
  ); /* Gradient background for the bottom border */
}

