/* Initial state */
.container {
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
  position: absolute;
  left: 50%; /* Start in the horizontal center of the page */
  top: 50%; /* Start in the vertical center of the page */
  transform: translate(-50%, -50%); /* Center the container */
  transition: all 0.5s ease; /* Add a smooth transition for animation */
}

.line {
  width: 1px;
  height: 550px;
  transform: rotate(10deg);
  background-image: linear-gradient(#000, white, #000);
  transform-origin: left center; /* Rotate around the left edge */
}

.arrow-button {
  background-color: transparent;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  transition: all 0.5s ease;
}

/* Final state (moved to the right) */
.container.right {
  left: calc(97% - 1px); /* Move the container to the right edge of the page */
}

.line.right {
  transform: none; /* Remove the transform property to make it straight */
}
