.penta1 {
  width: 166px;
  height: 210px;
  position: relative;
  margin: 24px;
  border: 2px solid rgb(11, 10, 10);
  overflow: visible;
}

.penta-image-container {
  position: absolute;
  top: -10px; /* Adjust as needed */
  left: -10px; /* Adjust as needed */
  width: calc(100% + 20px); /* Adjust as needed */
  height: calc(100% + 20px); /* Adjust as needed */
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}

.penta-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.penta-text {
  color: white;
  position: absolute;
  top: 85px;
  left: 8px;
  font-size: 22px;
  text-decoration: underline;
  font-family: "Rammeto One";
}
